



























	
	import {
	    mapState,
	    mapMutations
	} from 'vuex'
	
    export default {
        data() {
            return {
                tobook:'',
                sendDate: {
                   
                   frombook: ""
                }
            }
        },
        
			
			onLoad:function(e){
					console.log("e"+e.bname+e);
					this.tobook=e.bname;
					this.sendDate.receiver=e.bowner;
				},
        
        
		computed: {
			...mapState(['header','data'])
			},
        methods: {
			post() {
				console.log("进入提交");
				uni.showLoading({
					title: '提交中',
					mask: false
				});
				this.sendDate.tobook=this.tobook;
				this.sendDate.sender=this.data.nickname;
				
					console.log(this.sendDate);
				 if(this.sendDate.frombook ==''){
					 uni.showToast({
					 	title: '请补全信息',
					 	mask: false,
					 	duration: 1500
					 });
					 return;
				 }else{
					 uni.request({
					 	url: 'https://api.thinker.ink/v1/books/applications/publish/',
					 	method: 'POST',
					 	
						header:this.header,
					 	success: res => {
							console.log(res);
							uni.hideLoading();
							 if (res.statusCode === 201) {
							    uni.showToast({
							        title: "发布成功!"
							    });
							   
							    }else{
									uni.showToast({
									    title: "发布失败!"
									});
								}
							
							console.log(res);
							},
						
					 });
			
				 }
				
				}
           
            
        }
    }
